import * as React from "react"
import { graphql, Link } from "gatsby"
import { Helmet } from "react-helmet"
import Layout from "../../components/layout"
import "./index.module.scss"

const PortfolioPostTemplate = ({ data }) => {
  const { markdownRemark } = data
  const { frontmatter, html } = markdownRemark
  return (
    <Layout>
      <Helmet>
        <title>{frontmatter.title} - {frontmatter.date}</title>
      </Helmet>
      <section className="w-100 float-left blog-con padding-top padding-bottom position-relative" id="blog">
        <div className="container">
          <div className="blog-inner-con position-relative">
            <div className="generic-title text-center">
              <h6>Blog bài viết</h6>
              <h4 className="mb-0"><Link style={{color: "var(--text-color)"}} to="/#blog">Chia sẻ về blockchain</Link></h4>
              <div className="border-bottom pb-3 text-center mx-auto" style={{borderBottomColor: "var(--text-color) !important", width: '25px'}}></div>
            </div>
            <div className={`blog-box guide-section`} style={{color: "var(--text-color)"}}>
              <h2 className="text-center">{frontmatter.title}</h2>
              <p className="text-center">{frontmatter.date}</p>
              <div
                dangerouslySetInnerHTML={{ __html: html }}
              />
            </div>
          </div>
        </div>
      </section>
    </Layout>
  )
}

export default PortfolioPostTemplate

export const pageQuery = graphql`
  query($id: String!) {
    markdownRemark(id: { eq: $id }) {
      html
      frontmatter {
        date(formatString: "MMMM DD, YYYY")
        slug
        title
      }
    }
  }
`